.topbar {
    position: sticky;
    top: 0;
    background-color: #fff;
    width: 100vw;
    height: 50px;
    z-index: 999;
}

.topbar-wrraper{
    height: 100%;
    padding: 0 23px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.logo{
    color: 'darkblue' ;
    font-weight: 'bold' ; 
    font-size: 30px
}

.top-right{
    display: flex;
    align-items: center;
    gap: 10px;
}

.topbar-icon{
    cursor: pointer;
    color: #555 ;
}

.topbar-badge{
    font-size: 10px;
    right: 3px;
}

.topbar-avatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}