.widgetSm{
    flex: 1;
    padding: 20px;
    margin-right: 20px;
    box-shadow: 0 0 15px -10px rgba( 0 , 0 ,0 , 0.75);
}

.widgetSm-title{
 font-size: 22px;
 font-weight: 500;
}

.widgetSm-list{
    list-style: none;
}

.widgetsm-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;
}

.widgetSm-img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    object-fit: cover;
}

.widgetSm-user{
    display: flex;
    flex-direction: column;
    text-align: left;
}

.widgetSm-username{
    font-weight: 600;
}

.widgetSm-title{
    font-weight: 300;
}

.widgetsm-btn{
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    padding: 7px 10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;
}

.widgetsm-icon{
    font-size: 16px !important;
}
