.sidebar{
    flex: 1;
    height: 100vh;
    position: sticky;
    top: 50px;
    background-color: rgb(251, 251, 255);
}

.sidebar-wrapper{
    padding: 20px;
    color: #555;
}

.sidebar-menu{
    margin-bottom: 10px;
}

.sidebar-title{
    font-size: 14px;
    color: rgb(187, 186, 186);
}

.sidebar-list{
    list-style: none;
    padding: 5px;
}

.sidebar-list-item{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}
    
.sidebar-list-item.active , 
.sidebar-list-item:hover {
    background-color: rgb(240, 240, 255);
}

.sidebar-icon{
    margin-right: 5px;
    font-size: 20px;
}