.features{
    width: 100%;
    display: flex;
    justify-content: space-between;
    /* height: 180px; */
}


.features-item{
    flex: 1;
    margin: 0 20px;
    padding: 30px;
    box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.75);
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.feature-title{
    font-size: 22px;
}

.feature-container{
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.feature-money{
    font-size: 30px;
    font-weight: 500;
}

.feature-rate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.feature-sub{
    color: #555;
    font-weight: 500;
}

.features-icon{
    font-size: 26px;
    margin-left: 5px;
}

.negetive{
    color: rgb(255, 42, 42);
}
.positive{
    color: rgb(30, 136, 30);
}
