.widgetLg{
    flex: 2;
    padding: 20px;
    box-shadow: 0 0 15px -10px rgba(0 ,0 ,0 , 0.75);
}

.widgetLgTilte{
    font-size: 22px;
    font-weight: 600;
}

.widgetTable{
    width: 100%;
    border-spacing: 20px;
}

.widgetLgTr{

}

.widgetLgTh{
    text-align: left;
}

.widgetLgUser{
    display: flex;
    align-items: center;
}

.widgetLgImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    object-fit: cover;
    margin: 10px;
}

.customer{

}

.widgetLgDate ,
.widgetLgAmout{
    font-weight: 300;
}

.widgetLgBtn{
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}


.widgetLgBtn.Aprroved{
    color: #3bb077;
    background-color: #e5faf2;
}
.widgetLgBtn.Unaprroved{
    color: #d95087;
    background-color: #fff0f1;
}
.widgetLgBtn.Pendding{
    color: #2a7ade;
    background-color: #ebf1fe;
}