.products{
    flex: 4;
    padding: 20px;
}

.productTitleContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.productAddButton{
    width: 80px;
    border: none;
    padding: 5px;
    color: #fff;
    background: teal;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.productTop{
    display: flex;
}
 
.productLeft ,
.productRight {
    flex: 1;
}

.productRight{
    padding: 20px;
    margin: 20px;
    box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.75);
}

.productInfoTop{
    display: flex;
    align-items: center;
}

.productInfoImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 20px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.productInfoName{
    font-weight: 600;
}

.productInfoBottom{
    margin-top: 10px;
}

.productInfoItems{
    width: 150px;
    display: flex;
    justify-content: space-between;
}

.productInfoKey{
    font-weight: 600;
}

.productInfoValue{
    font-weight: 300;
}


.productBottom{
    padding: 20px;
    margin: 20px;
    box-shadow: 0 0 15px -10px rgba(0 , 0 , 0 , 0.75);
}

.productForm{
    display: flex;
    justify-content: space-between;
}

.productFormLeft{
    display: flex;
    flex-direction: column;
}

.productFormLeft > label{
    margin-bottom: 10px;
    color: gray;
}

.productFormLeft > input {
    margin-bottom: 10px;
    border: none;
    padding: 5px;
    border-bottom: 1px solid gray;
}

.productFormLeft > input:focus{
    outline: none;
}

.productFormLeft > select{
    margin-bottom: 10px;
}

.productFormRight{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.productUploader{
    display: flex;
    align-items: center;
}

.productUploaderImg{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    margin-right: 20px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.productUpdate{
    border: none;
    padding: 5px;
    border-radius: 5px;
    background-color: darkblue;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}